<template>
    <div class="index">
        <div class="content">
            <div class="index-lf">
                <div class="form-index">
                    <div class="form-title">
                        个人信息
                    </div>
                    <div class="form-box" v-if="!userInfo||!userInfo.verify_status">
                        <div class="unauthorized">
                            <img src="../assets/images/noData.jpg" alt="">
                        </div>
                        <div class="btn-box">
                            <div class="form-btn" @click="handleAuthorized">去实名认证</div>
                        </div>
                    </div>
                    <div class="form-box" v-else>
                        <el-form :model="userInfoform" status-icon :rules="rules" ref="ruleForm" label-width="100px">
                            <el-form-item label="姓名：" prop="address">
                                <span v-if="userInfo">{{userInfo.user_realname}}</span>
                            </el-form-item>
                            <el-form-item label="职业类型：" prop="phone">
                                <span v-if="userInfo">{{userInfo.workingidentifyStr}}</span>
                            </el-form-item>
                            <el-form-item label="工作年限：" prop="code">
                                <span v-if="userInfo">{{userInfo.workingAgeStr}}</span>
                            </el-form-item>
                            <el-form-item label="" prop="code">
                                <span class="verifying" v-if="userInfo&&userInfo.verify_status===1">实名认证审核中……</span>
                                <span class="verifyfail" v-if="userInfo&&userInfo.verify_status===-1">实名认证审核失败</span>
                            </el-form-item>
                            <div class="btn-box" v-if="userInfo&&userInfo.verify_status===-1">
                                <div class="form-btn" @click="handleAuthorized">重新审核</div>
                            </div>
                        </el-form>
                        <div class="btn-box">
                            <!--<div class="form-btn" @click="handleSubmit('ruleForm')">提交</div>-->
                        </div>

                        <!--<el-form :model="userInfoform" status-icon :rules="rules" ref="ruleForm" label-width="100px">-->
                        <!--<el-form-item label="国家/地区：" prop="address">-->
                        <!--<el-cascader :options="options" v-model="userInfo.address" :change-on-select="true"-->
                        <!--:clearable="true" :filterable="true" placeholder="请选择国家/地区"></el-cascader>-->
                        <!--</el-form-item>-->
                        <!--<el-form-item label="手机号：" prop="phone">-->
                        <!--<el-input v-model="userInfo.phone"></el-input>-->
                        <!--</el-form-item>-->
                        <!--<el-form-item label="验证码：" prop="code">-->
                        <!--<el-input v-model="userInfo.code">-->
                        <!--<span class="code" slot="append" @click="handleSend">发送验证码</span>-->
                        <!--</el-input>-->
                        <!--</el-form-item>-->
                        <!--</el-form>-->
                        <!--<div class="btn-box">-->
                        <!--<div class="form-btn" @click="handleSubmit('ruleForm')">提交</div>-->
                        <!--</div>-->
                    </div>
                </div>
                <div class="module-title">新人必看</div>
                <div class="video-box">
                    <img src="../assets/images/bg.png" alt="">
                </div>
            </div>
            <div class="index-rg">
                <div class="ranking-banner">
                    <el-carousel :interval="5000" arrow="always" height="200px">
                        <el-carousel-item v-for="(list,item) in bannerList" :key="item">
                            <div class="banner-img">
                                <a :href="list.link" target="_blank">
                                    <img :src="list.img_url" :title="list.title">
                                </a>
                                <!--<img src="https://cs.xinpianchang.com/uploadfile/banner/2019/04/30/5cc7fb9d8f473.jpg" alt="">-->
                            </div>
                        </el-carousel-item>
                    </el-carousel>
                </div>
                <div class="ranking-list">
                    <div class="ranking-header">
                        <div :class="currentInde===1?'active':''" @click="handleTab(1)">月收入排行榜</div>
                        <div :class="currentInde===2?'active':''" @click="handleTab(2)">月接单量排行榜</div>
                    </div>
                    <ul class="ranking-box" v-if="currentInde===1">
                        <li v-for="(list,index) in monthRanking" :key="index">
                            <span class="noun">V{{list.rankingNum}}</span>
                            <span class="portrait-box">
                                <img v-if="list.user_head" :src="list.user_head" alt="">
                                <img v-else src="../assets/images/default.jpg" alt="">
                            </span>
                            <span class="name" v-if="list.userName">{{list.userName}}</span>
                            <span class="name" v-else></span>
                            <span>{{list.income}}</span>
                        </li>
                    </ul>
                    <ul class="ranking-box" v-if="currentInde===2">
                        <li v-for="(list,index) in weekRanking" :key="index">
                            <span class="noun">V{{list.rankingNum}}</span>
                            <span class="portrait-box">
                                <img v-if="list.user_head" :src="list.user_head" alt="">
                            <img v-else src="../assets/images/default.jpg" alt="">
                                </span>
                            <span class="name" v-if="list.userName">{{list.userName}}</span>
                            <span class="name" v-else></span>
                            <span>{{list.orderedNum}}</span>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {
        mapState
    } from "vuex";
    import Http from '@/axios/http'
    import Api from '@/axios/api'
    import Ares from '../assets/js/area.js'

    export default {
        name: 'index',
        data() {
            var validatePhone = (rule, value, callback) => {
                let isMob = /^((\+?86)|(\+86))?(13[0123456789][0-9]{8}|15[0123456789][0-9]{8}||16[0123456789][0-9]{8}|17[0123456789][0-9]{8}|18[0123456789][0-9]{8}|14[0123456789][0-9]{8})$/;
                if (!value) {
                    return callback(new Error('手机号码不能为空'));
                } else {
                    if (isMob.test(value)) {
                        callback();
                    } else {
                        return callback(new Error('请输入正确的电话号码'));
                    }
                }
            };
            let validateCode = (rule, value, callback) => {
                if (value === '') {
                    callback(new Error('请输入验证码'));
                } else {
                    callback();
                }
            };
            return {
                options: Ares,
                currentInde: 1,
                userInfoform: {
                    address: [],
                    phone: '',
                    code: ''
                },
                rules: {
                    phone: [{
                        validator: validatePhone,
                        message: '手机号码不能为空',
                        trigger: 'blur'
                    }],
                    code: [{
                        validator: validateCode,
                        message: '请输入验证码',
                        trigger: 'blur'
                    }
                    ],
                    address: [{
                        message: '请选择国家',
                        trigger: 'blur'
                    }],
                },
                monthRanking: [],
                weekRanking: [],
                bannerList: []
            }
        },
        computed: mapState({
            userInfo: state => state.user.userInfo
        }),
        methods: {
            getBanner() {
                Http.post(null, Api.banner).then(res => {
                    let result = res.data
                    this.bannerList = [...result.data]
                })
                Http.post(null, 'http://hanml.artup.com/hanml-cms/webservice/classification/list?classtype=ysdbz&pageNo=1&pageSize=10').then(res => {
                    window.console.log('别的接口数据', res)
                })
            },
            getRanking() {
                let parma = {
                    userToken: localStorage.getItem('token')
                }
                Http.post(parma, Api.ranking).then(res => {
                    let result = res.data
                    let rankingData = result.data.data
                    this.monthRanking = []
                    this.weekRanking = []
                    if (rankingData) {
                        this.weekRanking = [...rankingData.weekRanking]
                        this.monthRanking = [...rankingData.monthRanking]
                    }
                })
            },
            handleAuthorized() {
                if (!localStorage.getItem('token')) {
                    this.$message.error('请先登录');
                    return false
                }
                this.$router.push({
                    path: '/certification'
                })
            },
            handleTab(index) {
                this.currentInde = index
            },
            handleSend() {
                if (!this.form.phone) {
                    this.$message.error('请输入手机号');
                    return false
                }
            },
            // handleSubmit(formName) {
            //     this.$refs[formName].validate((valid) => {
            //         if (valid) {
            //             alert('submit!');
            //         } else {
            //             return false;
            //         }
            //     });
            // }
        },
        mounted() {
            this.getBanner()
            this.getRanking()
            if (!this.userInfo || !this.userInfo.verify_status) {
                // this.$message.error('您还未完成实名认证哟，实名认证通过后您才可以接单')
                this.$message({
                    duration: 6000,
                    message: '您还未完成实名认证哟，实名认证通过后您才可以接单',
                    type: 'error'
                })
            }
        }
    }
</script>

<style lang="less" scoped>
    .index {
        width: 100%;
        /*height: 100%;*/
        height: auto;
        min-height: 100%;
        overflow: hidden;
        /*overflow-y: auto;*/
        display: block;
        padding-top: 20px;
        box-sizing: border-box;
        .index-lf, .index-rg {
            /*background: #FFFFFF;*/
            /*box-shadow: 2px 2px 10px 0 #DBD9DF;*/
            padding-bottom: 10px;
        }
        .index-lf {
            width: 750px;
            height: auto;
            display: block;
            float: left;
            margin-right: 40px;
            .form-index {
                width: 100%;
                height: 326px;
                display: block;
                background: #FFFFFF;
                box-shadow: 2px 2px 10px 0 #DBD9DF;
                .form-title {
