                        overflow: hidden;
                        margin: auto auto;
                    }
                }
            }
            .ranking-list {
                width: 100%;
                height: 546px;
                display: block;
                background: #FFFFFF;
                box-shadow: 2px 2px 10px 0 #DBD9DF;
                .ranking-header {
                    width: 100%;
                    height: 56px;
                    display: flex;
                    flex-direction: row;
                    > div {
                        flex: 1;
