                        display: block;
                        overflow: hidden;
                        border-bottom: 1px solid #eeeeee;
                        > span {
                            display: inline-block;
                            float: left;
                            height: 48px;
                            line-height: 48px;
                            font-size: 16px;
                            color: #000000;
                            width: 100px;
                            padding: 0 20px;
                            box-sizing: border-box;
                        }
                        > .noun {
                            width: 60px;
                        }
                        > .name {
                            width: 140px;
                        }
                        > .portrait-box {
                            position: relative;
                            width: 80px;
                            height: 40px;
                            display: block;
                            margin-top: 4px;
                            > img {
                                position: absolute;
                                left: 0;
                                right: 0;
                                top: 0;
                                bottom: 0;
                                max-height: 100%;
