<template>
    <div class="pay">
        <div class="pay-box" v-if="userInfo&&userInfo.zfb_accouant&&changFlag">
            <div class="accouant">
                <label for="" class="label">支付宝账号：</label>
                <span class="cont">{{userInfo.zfb_accouant}}</span>
                <span class="opction" @click="handleChange">修改</span></div>
        </div>
        <div class="pay-box" v-else>
            <el-input v-model="input" placeholder="请输入支付宝账号"></el-input>
            <div class="form-btn" @click="handleSumit">提交</div>
        </div>
    </div>
</template>

<script>
    import {
        mapState
    } from "vuex";
    import Http from '@/axios/http'
    import Api from '@/axios/api'

    export default {
        name: "pay",
        data() {
            return {
                input: '',
                changFlag: true
            }
        },
        computed: mapState({
            userInfo: state => state.user.userInfo
        }),
        methods: {
            handleSumit() {
                if (!this.input) {
                    this.$message.error('请输入支付宝账号');
                    return false
                }
                let param = {
                    userToken: localStorage.getItem('token'),
                    zfb_account: this.input
                }
                Http.post(param, Api.zfbSave).then(res => {
                    let result = res.data
                    if (result && Number(result.status) === 200) {
                        this.getUserinfo()
                        this.changFlag = true
                        this.$message({
                            message: result.msg,
                            type: 'success'
                        });
                    } else {
                        this.$message.error(result.msg);
                    }
                })
            },
            handleChange() {
                this.input = this.userInfo.zfb_accouant
                this.changFlag = false
            },
            getUserinfo() {
                let currentToken = localStorage.getItem('token') || '';
                let parma = {
                    userToken: currentToken
                }
                if (currentToken) {
                    Http.post(parma, Api.userInfo).then(res => {
                        let result = res.data
                        if (result && Number(result.status) === 200) {
                            this.$store.dispatch('addUserinfo', {
                                userInfo: result.data
                            });
                        } else {
                            this.$message.error(result.msg);
                        }
                    })
                }
            }
        }
    }
</script>

<style lang="less" scoped>
    .pay {
        width: 100%;
        height: 100%;
        margin: 0 auto;
        display: block;
        overflow: hidden;
        background: #FFFFFF;
        padding-top: 100px;
        box-sizing: border-box;
        .pay-box {
            width: 500px;
            height: auto;
            display: block;
            overflow: hidden;
            margin: 0 auto;
            .form-btn {
                width: 200px;
                height: 50px;
                line-height: 50px;
                font-size: 16px;
                color: #FFFFFF;
                text-align: center;
                background-image: linear-gradient(-135deg, #1909FE 0%, #8B24FC 100%);
                border-radius: 10px;
                cursor: pointer;
                margin: 0 auto;
                margin-top: 30px;
                box-shadow: 0 4px 12px 0px rgba(0, 0, 0, .1);
            }
            .accouant {
                width: 100%;
                height: auto;
                display: block;
                overflow: hidden;
                position: relative;
                padding-left: 110px;
                padding-right: 110px;
                box-sizing: border-box;
                .label, .opction {
                    position: absolute;
                    top: 0;
                    width: 100px;
                    line-height: 24px;
                    font-size: 16px;
                    color: #666;
                    overflow: hidden;
                    display: block;
                }
                .label {
                    left: 0;
                }
                .opction {
                    right: 0;
                    cursor: pointer;
                    color: #8638FD;
                    &:hover {
                        color: #9854ff;
                    }
                }
                .cont {
                    width: 100%;
                    height: auto;
                    line-height: 24px;
                    font-size: 16px;
                    color: #666;
                    overflow: hidden;
                    display: block;
                }
            }
        }
    }
</style>
